export default {
	data() {
		return {
			flag:true,
			xingJi:5,
			useUser:this.user,
			arr:[],
			url1:this.url1,
			useUrl:this.url1
		}
	},
	onLoad(options) {
	    this.get()
	},
	methods: {
		get:function(){
			let that=this;
			let usurl=that.url1+"/dinghai/comment/selectWorkComment";
			uni.request({
					url: usurl,
					header:{
						"Content-Type" : "application/x-www-form-urlencoded"
					},
					method:'POST',
					data: {
						userid:that.user.uid
					},
			    success: (res) => {
					console.log(res)
					if(res.statusCode==200){
						that.arr=res.data.flag
						for(let i=0; i<that.arr.length;i++){
							that.arr[i].commentImg=JSON.parse(that.arr[i].commentImg);
							that.arr[i].commentScore=Math.round(that.arr[i].commentScore)
						}
					}else{
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
			    },
				fail : function(err){
					uni.showToast({
						title:"您的网络打了个盹",
						icon:"none"
					})
				}
			});
		},
		back:function(){
			uni.navigateBack({})
		}
	}
}
